<template>
  <div v-if="isSupported">
    <button @click="loginWithWebAuthn">
      ورود با اثر انگشت
    </button>
  </div>
</template>

<script setup>
    import { ref, onMounted } from 'vue'
    import { sendApi } from '@/utils/api'
    import router from '@/router'
    const isSupported = ref(false)
    onMounted(() => {
        isSupported.value =
        window.PublicKeyCredential &&
        typeof window.PublicKeyCredential === 'function'
    })
    async function loginWithWebAuthn() {
        try {
            const res = await sendApi(JSON.stringify({
                action: 'login_webauthn_request'
            }))
            if (!res || !res.publicKey){
                if(res.status==='error')
                    alert(res.message);
                else
                    throw new Error('مشکلی در دریافت challenge رخ داد.')
            } 
            const options = preformatGetReq(res.publicKey)
            console.log(options);
            
            const assertion = await navigator.credentials.get({
                publicKey: options
            })
            console.log(assertion);
            
            const dataToSend = JSON.stringify({
                action: 'login_webauthn_response',
                id: assertion.id,
                rawId: arrayBufferToBase64(assertion.rawId),
                type: assertion.type,
                response: {
                    clientDataJSON: arrayBufferToBase64(assertion.response.clientDataJSON),
                    authenticatorData: arrayBufferToBase64(assertion.response.authenticatorData),
                    signature: arrayBufferToBase64(assertion.response.signature),
                    userHandle: assertion.response.userHandle
                    ? arrayBufferToBase64(assertion.response.userHandle)
                    : null,
                },
            })
            console.log(dataToSend);
            const verify = await sendApi(dataToSend)
            if (verify && verify.success) {
                router.push({name:'dashboard'})
            } else {
                alert('احراز هویت ناموفق بود.')
            }
        } catch (err) {
            console.error(err)
            alert('خطا در ورود با اثر انگشت')
        }
    }
    function arrayBufferToBase64(buffer) {
        return btoa(String.fromCharCode(...new Uint8Array(buffer)))
    }
    // function base64UrlToBase64(base64UrlString) {
    //     base64UrlString = base64UrlString.replace(/-/g, '+').replace(/_/g, '/');
    //     const padding = '='.repeat((4 - base64UrlString.length % 4) % 4);
    //     return base64UrlString + padding;
    // }
    // function base64UrlToArrayBuffer(base64UrlString) {
    //     const base64 = base64UrlToBase64(base64UrlString);
    //     const binaryString = atob(base64);
    //     const len = binaryString.length;
    //     const bytes = new Uint8Array(len);
    //     for (let i = 0; i < len; i++) {
    //         bytes[i] = binaryString.charCodeAt(i);
    //     }
    //     return bytes.buffer;
    // }
    function preformatGetReq(options) {
        const opts = {...options};
        opts.challenge = base64ToArrayBuffer(opts.challenge);
        if (opts.allowCredentials && Array.isArray(opts.allowCredentials)) {
            opts.allowCredentials = opts.allowCredentials.map(cred => ({
                ...cred,
                id: base64ToArrayBuffer(cred.id)
            }));
        }
        return opts;
    }

    function base64ToArrayBuffer(base64) {
        const binaryString = atob(base64);
        const len = binaryString.length;
        const bytes = new Uint8Array(len);
        for (let i = 0; i < len; i++) {
            bytes[i] = binaryString.charCodeAt(i);
        }
        return bytes.buffer;
    }
    // function preformatGetReq(options) {
    //     const opts = {...options};
    //     opts.challenge = base64UrlToArrayBuffer(opts.challenge);
    //     if (opts.allowCredentials && Array.isArray(opts.allowCredentials)) {
    //         opts.allowCredentials = opts.allowCredentials.map(cred => ({
    //         ...cred,
    //         id: base64UrlToArrayBuffer(cred.id)
    //         }));
    //     }
    //     return opts;
    // }
</script>
